export default function PlatformAgenciesPage() {
  const [agencies] = useState<Agency[]>(mockAgencies)
  const [searchQuery, setSearchQuery] = useState('')
  const [statusFilter, setStatusFilter] = useState<string>('all')
  const [planFilter, setPlanFilter] = useState<string>('all')
  const [sortBy, setSortBy] = useState<'name' | 'users' | 'revenue' | 'created'>('name')
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('asc')
  const [currentPage, setCurrentPage] = useState(1)
  const [selectedAgencies, setSelectedAgencies] = useState<string[]>([])
  const [expandedAgency, setExpandedAgency] = useState<string | null>(null)
  const itemsPerPage = 10

  // Filter agencies
  const filteredAgencies = agencies.filter(agency => {
    const matchesSearch = 
      agency.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      agency.owner.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
      agency.owner.email.toLowerCase().includes(searchQuery.toLowerCase())
    
    const matchesStatus = statusFilter === 'all' || agency.status === statusFilter
    const matchesPlan = planFilter === 'all' || agency.plan === planFilter
    
    return matchesSearch && matchesStatus && matchesPlan
  })

  // Sort agencies
  const sortedAgencies = [...filteredAgencies].sort((a, b) => {
    let comparison = 0
    
    switch (sortBy) {
      case 'name':
        comparison = a.name.localeCompare(b.name)
        break
      case 'users':
        comparison = a.totalUsers - b.totalUsers
        break
      case 'revenue':
        comparison = a.monthlyRevenue - b.monthlyRevenue
        break
      case 'created':
        comparison = new Date(a.createdAt).getTime() - new Date(b.createdAt).getTime()
        break
    }
    
    return sortOrder === 'asc' ? comparison : -comparison
  })

  // Pagination
  const totalPages = Math.ceil(sortedAgencies.length / itemsPerPage)
  const startIndex = (currentPage - 1) * itemsPerPage
  const paginatedAgencies = sortedAgencies.slice(startIndex, startIndex + itemsPerPage)

  // Statistics
  const stats = {
    total: agencies.length,
    active: agencies.filter(a => a.status === 'Active').length,
    inactive: agencies.filter(a => a.status === 'Inactive').length,
    suspended: agencies.filter(a => a.status === 'Suspended').length,
    totalUsers: agencies.reduce((sum, a) => sum + a.totalUsers, 0),
    totalRevenue: agencies.reduce((sum, a) => sum + a.monthlyRevenue, 0),
    totalSubAccounts: agencies.reduce((sum, a) => sum + a.subAccountsCount, 0),
  }

  // Plan distribution
  const planDistribution = {
    standard: agencies.filter(a => a.plan === 'Standard').length,
    professional: agencies.filter(a => a.plan === 'Professional').length,
    enterprise: agencies.filter(a => a.plan === 'Enterprise').length,
  }

  // Handle select/deselect
  const handleSelectAll = () => {
    if (selectedAgencies.length === paginatedAgencies.length) {
      setSelectedAgencies([])
    } else {
      setSelectedAgencies(paginatedAgencies.map(a => a.id))
    }
  }

  const handleSelectAgency = (id: string) => {
    setSelectedAgencies(prev =>
      prev.includes(id) ? prev.filter(aId => aId !== id) : [...prev, id]
    )
  }

  // Status badge colors
  const getStatusColor = (status: string) => {
    switch (status) {
      case 'Active': return 'bg-green-100 text-green-800'
      case 'Inactive': return 'bg-gray-100 text-gray-800'
      case 'Suspended': return 'bg-red-100 text-red-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  // Plan badge colors
  const getPlanColor = (plan: string) => {
    switch (plan) {
      case 'Standard': return 'bg-blue-100 text-blue-800'
      case 'Professional': return 'bg-purple-100 text-purple-800'
      case 'Enterprise': return 'bg-orange-100 text-orange-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
        <div>
          <nav className="flex items-center text-sm text-gray-500 mb-2">
            <Link href="/sub-account/platform-admin" className="hover:text-gray-700">
              Platform Admin
            </Link>
            <span className="mx-2">/</span>
            <span className="text-gray-900">Agencies</span>
          </nav>
          <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
            <span className="text-2xl">üè¢</span>
            Agency Management
          </h1>
          <p className="text-gray-500 text-sm mt-1">
            Manage all agencies, their sub-accounts, and subscriptions
          </p>
        </div>
        <div className="flex items-center gap-2">
          <Link
            href="/sub-account/platform-admin/agencies/new"
            className="px-4 py-2 bg-purple-600 text-white rounded-lg hover:bg-purple-700 flex items-center gap-2"
          >
            <span>‚ûï</span>
            Add Agency
          </Link>
        </div>
      </div>

      {/* Statistics Cards */}
      <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-7 gap-4">
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-purple-600">{stats.total}</div>
          <div className="text-sm text-gray-600">Total Agencies</div>
        </div>
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-green-600">{stats.active}</div>
          <div className="text-sm text-gray-600">Active</div>
        </div>
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-gray-600">{stats.inactive}</div>
          <div className="text-sm text-gray-600">Inactive</div>
        </div>
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-red-600">{stats.suspended}</div>
          <div className="text-sm text-gray-600">Suspended</div>
        </div>
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-blue-600">{stats.totalSubAccounts}</div>
          <div className="text-sm text-gray-600">Sub-Accounts</div>
        </div>
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-indigo-600">{stats.totalUsers.toLocaleString()}</div>
          <div className="text-sm text-gray-600">Total Users</div>
        </div>
        <div className="bg-white p-4 rounded-xl border border-gray-200 shadow-sm">
          <div className="text-2xl font-bold text-amber-600">${stats.totalRevenue.toLocaleString()}</div>
          <div className="text-sm text-gray-600">Monthly Revenue</div>
        </div>
      </div>

      {/* Plan Distribution */}
      <div className="bg-white rounded-xl border border-gray-200 shadow-sm p-4">
        <h3 className="font-medium text-gray-900 mb-3">Plan Distribution</h3>
        <div className="grid grid-cols-3 gap-4">
          <div className="flex items-center justify-between p-3 bg-blue-50 rounded-lg">
            <div>
              <p className="font-medium text-blue-800">Standard</p>
              <p className="text-sm text-blue-600">${49}/mo</p>
            </div>
            <div className="text-2xl font-bold text-blue-700">{planDistribution.standard}</div>
          </div>
          <div className="flex items-center justify-between p-3 bg-purple-50 rounded-lg">
            <div>
              <p className="font-medium text-purple-800">Professional</p>
              <p className="text-sm text-purple-600">${249}/mo</p>
            </div>
            <div className="text-2xl font-bold text-purple-700">{planDistribution.professional}</div>
          </div>
          <div className="flex items-center justify-between p-3 bg-orange-50 rounded-lg">
            <div>
              <p className="font-medium text-orange-800">Enterprise</p>
              <p className="text-sm text-orange-600">${449}/mo</p>
            </div>
            <div className="text-2xl font-bold text-orange-700">{planDistribution.enterprise}</div>
          </div>
        </div>
      </div>

      {/* Bulk Actions */}
      {selectedAgencies.length > 0 && (
        <div className="bg-purple-50 border border-purple-200 rounded-xl p-4 flex items-center justify-between">
          <span className="text-sm font-medium text-purple-800">
            {selectedAgencies.length} agenc{selectedAgencies.length > 1 ? 'ies' : 'y'} selected
          </span>
          <div className="flex items-center gap-2">
            <button className="px-3 py-1.5 bg-green-600 text-white rounded-lg text-sm hover:bg-green-700">
              Activate
            </button>
            <button className="px-3 py-1.5 bg-gray-600 text-white rounded-lg text-sm hover:bg-gray-700">
              Deactivate
            </button>
            <button className="px-3 py-1.5 bg-red-600 text-white rounded-lg text-sm hover:bg-red-700">
              Suspend
            </button>
            <button className="px-3 py-1.5 bg-blue-600 text-white rounded-lg text-sm hover:bg-blue-700">
              Export
            </button>
            <button
              onClick={() => setSelectedAgencies([])}
              className="px-3 py-1.5 bg-white text-gray-700 border border-gray-300 rounded-lg text-sm hover:bg-gray-50"
            >
              Cancel
            </button>
          </div>
        </div>
      )}

      {/* Filters */}
      <div className="bg-white rounded-xl border border-gray-200 shadow-sm p-4">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          {/* Search */}
          <div className="md:col-span-2">
            <div className="relative">
              <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                <svg className="h-5 w-5 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z" />
                </svg>
              </div>
              <input
                type="text"
                placeholder="Search agencies, owners, emails..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="block w-full pl-10 pr-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
              />
            </div>
          </div>

          {/* Status Filter */}
          <select
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
          >
            <option value="all">All Status</option>
            <option value="Active">Active</option>
            <option value="Inactive">Inactive</option>
            <option value="Suspended">Suspended</option>
          </select>

          {/* Plan Filter */}
          <select
            value={planFilter}
            onChange={(e) => setPlanFilter(e.target.value)}
            className="px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-purple-500 focus:border-transparent"
          >
            <option value="all">All Plans</option>
            <option value="Standard">Standard ($49)</option>
            <option value="Professional">Professional ($249)</option>
            <option value="Enterprise">Enterprise ($449)</option>
          </select>
        </div>

        {/* Sort Options */}
        <div className="mt-4 pt-4 border-t border-gray-200 flex items-center justify-between">
          <span className="text-sm text-gray-500">
            Showing {paginatedAgencies.length} of {filteredAgencies.length} agencies
          </span>
          <div className="flex items-center gap-2">
            <span className="text-sm text-gray-500">Sort by:</span>
            {(['name', 'users', 'revenue', 'created'] as const).map((option) => (
              <button
                key={option}
                onClick={() => {
                  if (sortBy === option) {
                    setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc')
                  } else {
                    setSortBy(option)
                    setSortOrder('asc')
                  }
                }}
                className={`px-3 py-1.5 rounded-lg text-sm ${
                  sortBy === option
                    ? 'bg-purple-600 text-white'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                {option.charAt(0).toUpperCase() + option.slice(1)}
                {sortBy === option && (sortOrder === 'asc' ? ' ‚Üë' : ' ‚Üì')}
              </button>
            ))}
          </div>
        </div>
      </div>

      {/* Agencies Table */}
      <div className="bg-white rounded-xl border border-gray-200 shadow-sm overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-gray-50 border-b border-gray-200">
              <tr>
                <th className="px-4 py-3 text-left">
                  <input
                    type="checkbox"
                    checked={selectedAgencies.length === paginatedAgencies.length && paginatedAgencies.length > 0}
                    onChange={handleSelectAll}
                    className="rounded border-gray-300 text-purple-600 focus:ring-purple-500"
                  />
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Agency
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Owner
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Plan
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Status
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Sub-Accounts
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Users
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Revenue
                </th>
                <th className="px-4 py-3 text-left text-xs font-semibold text-gray-600 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-200">
              {paginatedAgencies.map((agency) => (
                <React.Fragment key={agency.id}>
                  <tr className="hover:bg-gray-50">
                    <td className="px-4 py-4">
                      <input
                        type="checkbox"
                        checked={selectedAgencies.includes(agency.id)}
                        onChange={() => handleSelectAgency(agency.id)}
                        className="rounded border-gray-300 text-purple-600 focus:ring-purple-500"
                      />
                    </td>
                    <td className="px-4 py-4">
                      <div className="flex items-center gap-3">
                        <div className="w-10 h-10 bg-purple-100 rounded-lg flex items-center justify-center text-purple-600 font-bold">
                          {agency.name.charAt(0)}
                        </div>
                        <div>
                          <div className="font-medium text-gray-900">{agency.name}</div>
                          <div className="text-sm text-gray-500">{agency.id}</div>
                        </div>
                      </div>
                    </td>
                    <td className="px-4 py-4">
                      <div className="font-medium text-gray-900">{agency.owner.name}</div>
                      <div className="text-sm text-gray-500">{agency.owner.email}</div>
                    </td>
                    <td className="px-4 py-4">
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${getPlanColor(agency.plan)}`}>
                        {agency.plan}
                      </span>
                      {agency.plan === 'Enterprise' && agency.telephonyMarkup > 0 && (
                        <div className="text-xs text-gray-500 mt-1">+{agency.telephonyMarkup}% markup</div>
                      )}
                    </td>
                    <td className="px-4 py-4">
                      <span className={`px-2 py-1 rounded-full text-xs font-medium ${getStatusColor(agency.status)}`}>
                        {agency.status}
                      </span>
                    </td>
                    <td className="px-4 py-4">
                      <button
                        onClick={() => setExpandedAgency(expandedAgency === agency.id ? null : agency.id)}
                        className="flex items-center gap-1 text-purple-600 hover:text-purple-800"
                      >
                        <span className="font-semibold">{agency.subAccountsCount}</span>
                        <span className="text-gray-400 text-sm">
                          / {agency.subAccountsLimit === 'Unlimited' ? '‚àû' : agency.subAccountsLimit}
                        </span>
                        <svg className={`w-4 h-4 transition-transform ${expandedAgency === agency.id ? 'rotate-180' : ''}`} fill="none" viewBox="0 0 24 24" stroke="currentColor">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                        </svg>
                      </button>
                    </td>
                    <td className="px-4 py-4">
                      <span className="font-semibold text-gray-900">{agency.totalUsers}</span>
                    </td>
                    <td className="px-4 py-4">
                      <span className="font-semibold text-green-600">${agency.monthlyRevenue}</span>
                      <span className="text-gray-500 text-sm">/mo</span>
                    </td>
                    <td className="px-4 py-4">
                      <div className="flex gap-2">
                        <Link
                          href={`/sub-account/platform-admin/agencies/${agency.id}`}
                          className="px-3 py-1 bg-purple-600 text-white rounded-lg text-sm hover:bg-purple-700"
                        >
                          View
                        </Link>
                        <button className="px-3 py-1 bg-gray-100 text-gray-700 rounded-lg text-sm hover:bg-gray-200">
                          Edit
                        </button>
                      </div>
                    </td>
                  </tr>
                  {/* Expanded Sub-Accounts */}
                  {expandedAgency === agency.id && (
                    <tr>
                      <td colSpan={9} className="px-4 py-4 bg-gray-50">
                        <div className="ml-12">
                          <h4 className="font-medium text-gray-900 mb-3">Sub-Accounts ({agency.subAccountsCount})</h4>
                          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-3">
                            {agency.subAccounts.map((sub) => (
                              <div key={sub.id} className="bg-white p-3 rounded-lg border border-gray-200 flex items-center justify-between">
                                <div>
                                  <p className="font-medium text-gray-900">{sub.name}</p>
                                  <p className="text-sm text-gray-500">{sub.type} ‚Ä¢ {sub.users} users</p>
                                </div>
                                <span className={`px-2 py-0.5 rounded-full text-xs font-medium ${
                                  sub.status === 'Active' ? 'bg-green-100 text-green-700' : 'bg-gray-100 text-gray-600'
                                }`}>
                                  {sub.status}
                                </span>
                              </div>
                            ))}
                          </div>
                        </div>
                      </td>
                    </tr>
                  )}
                </React.Fragment>
              ))}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        <div className="bg-gray-50 px-4 py-3 border-t border-gray-200 flex items-center justify-between">
          <div className="text-sm text-gray-600">
            Showing {startIndex + 1} to {Math.min(startIndex + itemsPerPage, sortedAgencies.length)} of {sortedAgencies.length} agencies
          </div>
          <div className="flex gap-2">
            <button
              onClick={() => setCurrentPage(prev => Math.max(1, prev - 1))}
              disabled={currentPage === 1}
              className="px-3 py-1 border border-gray-300 rounded-lg text-sm hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Previous
            </button>
            {Array.from({ length: totalPages }, (_, i) => i + 1).map(page => (
              <button
                key={page}
                onClick={() => setCurrentPage(page)}
                className={`px-3 py-1 rounded-lg text-sm ${
                  currentPage === page
                    ? 'bg-purple-600 text-white'
                    : 'border border-gray-300 hover:bg-gray-100'
                }`}
              >
                {page}
              </button>
            ))}
            <button
              onClick={() => setCurrentPage(prev => Math.min(totalPages, prev + 1))}
              disabled={currentPage === totalPages}
              className="px-3 py-1 border border-gray-300 rounded-lg text-sm hover:bg-gray-100 disabled:opacity-50 disabled:cursor-not-allowed"
            >
              Next
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}
